(function () {
    'use strict';

    angular
        .module('rb.books')
        .controller('BooksController', BooksController);

    /** @ngInject */
    function BooksController($state,BooksService) {

        var vm = this;
        vm.books = [];
        activate();

        function activate() {
           getListBooks();
        }
        function getListBooks(){
            vm.books = BooksService.getList();
        }


    }

})();

